import React,  { useState, useEffect }  from "react";
import { Link } from "gatsby";
import * as Icon from 'react-feather';

import '../styles/navbar.scss';

function Header() {
  const [collapsed, setCollapsed] = useState(true);

  useEffect(() => {
    let elementId = document.getElementById("navbar");
      document.addEventListener("scroll", () => {
        if (window.scrollY > 170) {
          elementId.classList.add("is-sticky");
      } else {
          elementId.classList.remove("is-sticky");
      }
    })
    window.scrollTo(0, 0);
  }, []);

  function navbarToggle() {
    setCollapsed(!collapsed);
  }

  const classOne = collapsed ? 'collapse navbar-collapse' : 'collapse navbar-collapse show';
  const classTwo = collapsed ? 'navbar-toggler navbar-toggler-right collapsed' : 'navbar-toggler navbar-toggler-right';

  return(
    <header id="header">
      <div id="navbar" className="startp-nav">
        <div className="container">
          <nav className="navbar navbar-expand-md navbar-light">
            <Link to="/" className="navbar-brand" onClick={() => window.location.refresh()}>
              <img src={require("../images/logo.png")} alt="logo" />
            </Link>
            <button 
              onClick={navbarToggle} 
              className={classTwo}
              type="button" 
              data-toggle="collapse" 
              data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" 
              aria-expanded="false" 
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className={classOne} id="navbarSupportedContent">
              <ul className="navbar-nav nav ml-auto">
                <li className="nav-item">
                  <Link activeClassName="active" to="/" className="nav-link">
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <div className="nav-dropdown">
                    Industry <Icon.ChevronDown />
                  </div>
                  <ul className="dropdown_menu">
                    <li className="nav-item">
                      <Link activeClassName="active" to="/features" className="nav-link">
                        Features
                      </Link>
                    </li>
                    <li className="nav-item">
                      <Link activeClassName="active" className="nav-link" to="/team">
                        Team
                      </Link>
                    </li>
                    <li className="nav-item">
                      <Link activeClassName="active" to="/pricing" className="nav-link">
                        Pricing
                      </Link>
                    </li>
                  </ul>
                </li>
                <li className="nav-item">
                  <div className="nav-dropdown">Application <Icon.ChevronDown /></div>
                  <ul className="dropdown_menu">
                      <li className="nav-item">
                        <Link activeClassName="active" to="/products" className="nav-link">
                          Products
                        </Link>
                      </li>
                      <li className="nav-item">
                        <Link activeClassName="active" to="/product-details" className="nav-link">
                          Products Details
                        </Link>
                      </li>
                  </ul>
                </li>
                <li className="nav-item">
                  <Link activeClassName="active" to="/news" className="nav-link">
                    News
                  </Link>
                </li>
                <li className="nav-item">
                  <Link activeClassName="active" to="/contact" className="nav-link">
                    Contact Us
                  </Link>
                </li>
              </ul>
            </div>
          </nav>
        </div> 
      </div>
    </header>
  )
}

export default Header;
